        kjt_pay: '快捷通',
        sg_ious: '智家白条',
        epay: '大额支付',
        bank_pay: '银行卡',
        b2b_pay: '公对公支付',
        digiccy: '数字人民币',
        aggre_pay: '聚合扫码支付',
        cash_card: '现金刷卡',
      },

      isWeikuan: false, // 是否是尾款支付
      leftTime: '', // 倒计时
      price: '', // 支付金额
      totalAmount: '', // 商品总金额
      curSelected: '', // 当前选中的支付方式
      curSelBank: '', // 当前选中的银行卡支付方式
      curSelBankId: '', // 当前选中的银行卡id
      // curSelectedCodes: '', // 当前支付方式的中台codes
      orderId: '', // 订单id
      orderSn: '', // 订单号
      minPaymentAmt: '', // 最小支付金额

      paymethodsTopVOList: [],
      paymethodsMiddleVOList: [],
      paymethodsBottomVOList: [],

      cardlists: [], // 已绑定的银行卡

      initLinkType: 0, // 支付方式首次渲染时 微信间连(1)显示 直连(0)不显示
      linkType: 0, // 支付方式是否直连 0:直连  1:间连
      platformCode: '', // 中台支付方式编码
      platformParentCode: '', // 中台渠道编码

      stagepCreditId: '', // 分期id
      stageIndex: 0, // 分期索引
      curClickStage: '', // 当前点击的分期
      stageNum: '', // 分期数
      stagecfgMark: '', // 建行光大 分期mark
      discountPrice: 0, // 优惠金额

      isToggle: false, // 其他支付方式切换
      unionPayFormData: {}, // 云闪付表单数据

      aliPayFormData: {}, // 支付宝间连表单数据
      weixinFormData: {}, // 微信支付表单数据
      epayFormData: {}, // 工行大额支付
      digiccyFormData: {}, // 数字人民币

      isPaying: false, // 微信是否正在支付中
      isSubmiting: false, // 是否正在提交支付

      isCanShowVersion: false, // 是否是可用版本展示
      isShowEPay: true, // 是否展示大额支付
      isShowAlipay: true, // 是否展示支付宝支付
      aliPayType: 'h5', // 阿里支付方式

