      numKeybordShow: false, //
      // smsCode: [], // 短信验证码
      mobileNo: '', // 银行卡支付手机号
      bankTel: '', // 银行电话
      preGetTime: 0, // 验证码发送时间
      resetGetSms: false, // 是否重置
      leaveOrderTip: false, // 离开确认

      curDigitCard: '', // 当前选中的数字人民币支付卡
