      this.paymethodsTopVOList.forEach(item => {
        item.stagesrepaidTotalAmt = item.stagesVOList.length ? item.stagesVOList[0].repaidTotalAmt : ''
        // 数字人民币
        if (item.code === 'digiccy') {
          this.digitCardList = item.digiccyInfoResponseList
        }
        // 屏蔽微信间连支付
        // if (item.code === 'wx_pay' && this.linkType === 0 && !util.isWeixin()) {
        //   this.hasinDirectWxPay = true
        // } else if (item.code === 'wx_pay' && this.linkType === 0 && util.isWeixin()) {
        //   this.hasinDirectWxPay = false
        // }
      })
      this.paymethodsMiddleVOList.forEach(item => {
        item.stagesrepaidTotalAmt = item.stagesVOList.length ? item.stagesVOList[0].repaidTotalAmt : ''
        // 数字人民币
        if (item.code === 'digiccy') {
          this.digitCardList = item.digiccyInfoResponseList
        }
        // 屏蔽微信间连支付
        // if (item.code === 'wx_pay' && this.linkType === 0 && !util.isWeixin()) {
        //   this.hasinDirectWxPay = true
        // } else if (item.code === 'wx_pay' && this.linkType === 0 && util.isWeixin()) {
        //   this.hasinDirectWxPay = false
        // }
      })
      this.paymethodsBottomVOList.forEach(item => {
        item.stagesrepaidTotalAmt = item.stagesVOList.length ? item.stagesVOList[0].repaidTotalAmt : ''
        // 数字人民币
        if (item.code === 'digiccy') {
          this.digitCardList = item.digiccyInfoResponseList
        }
        // 屏蔽微信间连支付
        // if (item.code === 'wx_pay' && this.linkType === 0 && !util.isWeixin()) {
        //   this.hasinDirectWxPay = true
        // } else if (item.code === 'wx_pay' && this.linkType === 0 && util.isWeixin()) {
        //   this.hasinDirectWxPay = false
        // }
      })

      this.linkType = this.initLinkType = this.paymethodsTopVOList[0] ? this.paymethodsTopVOList[0].checkoutType : this.paymethodsMiddleVOList[0] ? this.paymethodsMiddleVOList[0].checkoutType : this.paymethodsBottomVOList[0].checkoutType

      this.platformCode = this.paymethodsTopVOList[0] ? this.paymethodsTopVOList[0].middlePlatformCode : this.paymethodsMiddleVOList[0] ? this.paymethodsMiddleVOList[0].middlePlatformCode : this.paymethodsBottomVOList[0].middlePlatformCode

      this.platformParentCode = this.paymethodsTopVOList[0] ? this.paymethodsTopVOList[0].middlePlatformParentCode : this.paymethodsMiddleVOList[0] ? this.paymethodsMiddleVOList[0].middlePlatformParentCode : this.paymethodsBottomVOList[0].middlePlatformParentCode

      this.curSelected = this.paymethodsTopVOList[0] ? this.paymethodsTopVOList[0].code : this.paymethodsMiddleVOList[0] ? this.paymethodsMiddleVOList[0].code : this.paymethodsBottomVOList[0].code

      // 如果ios14且app 6.19之前的 设置默认微信支付
      if (!this.isShowAlipay) {
        this.curSelected = 'wx_pay'
      }

      // this.curSelectedCodes = this.paymethodsTopVOList[0].middlePlatformCodes
      this.minPaymentAmt = this.paymethodsTopVOList[0] ? this.paymethodsTopVOList[0].minPaymentAmt : this.paymethodsMiddleVOList[0] ? this.paymethodsMiddleVOList[0].minPaymentAmt : this.paymethodsBottomVOList[0] ? this.paymethodsBottomVOList[0].minPaymentAmt : ''

      let oldPayMethod = this.$util.strJSONParse(sessionStorage.getItem('cur_pay_method'), {})
      if (oldPayMethod && oldPayMethod[this.orderSn] && !clearsession) {
        let json = oldPayMethod[this.orderSn]
        if (json.code === 'bank_pay') {
          this.curSelected = json.code
          this.curSelBank = json.bankCode
          this.curSelBankId = json.id
          this.mobileNo = json.mobileNo
          this.bankTel = json.bankTel
          this.resetGetSms = true
        } else {
          this.linkType = json.checkoutType
          this.platformCode = json.middlePlatformCode
          this.platformParentCode = json.middlePlatformParentCode
          this.stagecfgMark = json.cfgMark
          this.curSelected = json.code
          this.curClickStage = json.code
          this.discountPrice = Number(json.discountAmt || null)
          this.minPaymentAmt = json.minPaymentAmt
          this.isSubmiting = false
        }
      }
