    fetchData() {
      let env = util.isWeixin() ? '4' : '2'

      let url = `${URL.TO_ORDER_SUBMIT_SUCCESS_NEW}?channel=sg&terminalId=${env}&orderNo=${this.orderSn}`
      this.$http.post(url).then(res => {
        this.skeletonLoading = false
        if (res.data.success) {
          let redata = res.data.data;
          this.price = this.totalAmount = redata.orderAmt.toFixed(2)
          this.isWeikuan = redata.orderType === 2
          if (redata.remainTime < 0 && !this.isWeikuan && redata.orderType !== 22) {
            this.$router.push('/payState')
            // this.$router.replace('/orderPay?type=submit')
            return
          }
          if (!this.isWeikuan) {
            this.intervalLeft(redata.remainTime)
          }
          this.orderId = redata.orderId
          this.paymethodsTopVOList = redata.paymethodsTopVOList
          this.paymethodsMiddleVOList = redata.paymethodsMiddleVOList
          this.paymethodsBottomVOList = redata.paymethodsBottomVOList
          this.readyInit()

          // 获取绑卡信息
          this.fetchBindBankCard()
        } else {
          this.$toast(res.data.message)
        }
      })
    },
    checkOrderStatus() {
      let env = util.isWeixin() ? '4' : '2'

      let url = `${URL.TO_ORDER_SUBMIT_SUCCESS_NEW}?channel=sg&terminalId=${env}&orderNo=${this.orderSn}`
      this.$http.post(url).then(res => {
        if (res.data.success && res.data.data && res.data.data.payStatus === 101) { // 100未支付，101已支付
          // this.$router.replace('/orderList?tagChecked=2')
          this.$router.replace('/payState?data={"is_success":"T","order_id": "' + this.orderId + '"}')
        }
      })
    },

